  async setCountryAgent(countryCode, agent) {
    try{
      if (!this.isMobile && WalletService.walletType !== 'walletconnect') {
        const contract = await this.getNativeContract();
        const tx = await contract.setCountryAgent(countryCode, agent);
        await tx.wait();
        return true;
      }

      const readOnlyContract = await this.getReadOnlyContract();
      const tx = await readOnlyContract.setCountryAgent(countryCode, agent);
      await tx.wait();
      return true;

    } catch(error){
      console.error("Error setting country agent:", error);
      return false;
    }
  }
  
  
  /**
   * Gets ticket count
   */
  async getTicketCount() {
    try {
      if (!this.isMobile && WalletService.walletType !== 'walletconnect') {
        try {
          const contract = await this.getNativeContract();
          const count = await contract.getTicketCount();
          return count;
        } catch (nativeError) {
          //console.error("Failed to get ticket count with native wallet:", nativeError);
        }
      }
      
      const readContract = await this.getReadOnlyContract();
      const count = await readContract.getTicketCount();
      return count;
    } catch (error) {
      //console.error("Error fetching ticket count:", error);
      throw error;
    }
  }
  
  /**
   * Gets current round
   */
  async getCurrentRound() {
    try {
      if (!this.isMobile && WalletService.walletType !== 'walletconnect') {
        try {
          const contract = await this.getNativeContract();
          const round = await contract.round();
          return round;
        } catch (nativeError) {
          //console.error("Failed to get current round with native wallet:", nativeError);
        }
      }
      
      const readContract = await this.getReadOnlyContract();
      const round = await readContract.round();
      return round;
    } catch (error) {
      //console.error("Error fetching current round:", error);
      throw error;
    }
  }
